/*********** Nav.js **********/
nav {
	display: flex;
	justify-content: space-around;
	align-items: center;
	min-height: 10vh;
	background: rgb(73, 79, 82);
	color: white;
}

.nav-links {
	width: 50%;
	display: flex;
	margin-top: 5px;
	justify-content: space-around;
	align-items: center;
	list-style: none;
}

li {
	color: #fff !important;
	text-decoration: none !important;
}
/******  Nav.js *********/

/********* Home.js ********/
.home {
	background-color: #ededed;
	display: flex;
	min-height: 100vh;
	width: 100%;
	margin: auto;
	padding: 0;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}
/********** Home.js *******/

/******** Quiz.js *********/
.questions {
	margin-top: 20px;
	text-align: center;
	background-color: #ededed;
	padding: 20px;
	border-left: 5px solid blue;
}

.options-container {
	display: inline-block;
	width: 30%;
}
.option {
	background-color: rgb(156, 156, 231);
	border-radius: 10px;
	margin: 10px 10px;
	padding: 2px;
	cursor: pointer;
	transition: 0.5s ease-in-out;
}
.option:hover {
	background-color: rgb(73, 73, 163);
	color: #fff;
}

.disable {
	background-color: #ccc;
	box-shadow: none;
	pointer-events: none;
}
/******** Quiz.js *********/

/******** AddQuiz.js ********/
.form-div {
	margin: 50px auto;
	background-color: #ededed;
	padding: 30px;
	width: 40%;
}
